/* ==== CSS Variables ==== */
:root {
  --primary: #3b82f6;
  --primary-dark: #2563eb;
  --primary-light: #60a5fa;
  --primary-gradient: linear-gradient(135deg, #3b82f6, #8b5cf6);
  --success: #10b981;
  --success-light: #34d399;
  --danger: #ef4444;
  --warning: #f59e0b;
  --dark-bg: hsl(210, 30%, 8%);
  --dark-card: rgba(255, 255, 255, 0.07);
  --text-primary: #f0f4f8;
  --text-secondary: #94a3b8;
  --border-color: rgba(255, 255, 255, 0.1);
}

/* ==== Global Reset ==== */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body,
#root {
  height: 100%;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background: linear-gradient(135deg, hsl(210, 30%, 12%), hsl(210, 30%, 8%));
  color: var(--text-primary);
  overflow-x: hidden;
}

/* ==== Utility Classes ==== */
.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.text-center {
  text-align: center;
}

.text-secondary {
  color: var(--text-secondary);
}

/* ==== Cards ==== */
.card {
  background: var(--dark-card);
  backdrop-filter: blur(16px);
  border-radius: 16px;
  padding: 2rem;
  border: 1px solid var(--border-color);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.card-glass {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.5);
  border-color: rgba(255, 255, 255, 0.15);
}

.card-clickable {
  cursor: pointer;
}

.card-clickable:active {
  transform: translateY(-2px);
}

/* ==== Buttons ==== */
.btn {
  display: inline-block;
  padding: 0.875rem 1.75rem;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  border: none;
  font-size: 1rem;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.btn:hover::before {
  width: 300px;
  height: 300px;
}

.btn-primary {
  background: var(--primary-gradient);
  color: #fff;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(59, 130, 246, 0.4);
}

.btn-primary:active {
  transform: scale(0.98);
}

.btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.2);
}

.btn-success {
  background: linear-gradient(135deg, var(--success), var(--success-light));
  color: #fff;
}

.btn-lg {
  padding: 1.125rem 2.25rem;
  font-size: 1.125rem;
}

.btn-sm {
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
}

/* ==== Forms ==== */
.form-group {
  margin-bottom: 1.5rem;
}

.form-label {
  display: block;
  margin-bottom: 0.625rem;
  font-weight: 600;
  color: var(--text-primary);
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-input,
.form-select {
  width: 100%;
  padding: 0.875rem 1.125rem;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  font-size: 1rem;
  transition: all 0.2s ease;
}

.form-input:focus,
.form-select:focus {
  outline: none;
  border-color: var(--primary);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-input::placeholder {
  color: var(--text-secondary);
}

.form-select {
  cursor: pointer;
}

/* ==== Alerts ==== */
.alert {
  padding: 1rem 1.25rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  font-size: 0.9375rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.alert-error {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid var(--danger);
  color: #fca5a5;
}

.alert-success {
  background: rgba(16, 185, 129, 0.1);
  border: 1px solid var(--success);
  color: #6ee7b7;
}

.alert-warning {
  background: rgba(245, 158, 11, 0.1);
  border: 1px solid var(--warning);
  color: #fcd34d;
}

/* ==== Modal Overlay & Container ==== */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  backdrop-filter: blur(4px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.2s ease;
}

.modal {
  background: linear-gradient(135deg, hsl(210, 30%, 16%), hsl(210, 30%, 12%));
  border: 1px solid var(--border-color);
  border-radius: 20px;
  max-width: 500px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  animation: slideUp 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-header {
  padding: 2rem;
  border-bottom: 1px solid var(--border-color);
  position: relative;
}

.modal-title {
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.modal-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: var(--text-secondary);
  font-size: 1.5rem;
  width: 36px;
  height: 36px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-close:hover {
  background: rgba(255, 255, 255, 0.15);
  color: var(--text-primary);
}

.modal form {
  padding: 2rem;
}

/* ==== Animations ==== */
.fade-in {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
  }

  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* ==== Dashboard Layout ==== */
.dashboard {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
  min-height: 100vh;
}

.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.5rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.dashboard-title {
  font-size: 2.25rem;
  font-weight: 700;
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.balance-card {
  background: var(--primary-gradient);
  padding: 2.5rem;
  border-radius: 20px;
  margin-bottom: 2.5rem;
  text-align: center;
  box-shadow: 0 8px 32px rgba(59, 130, 246, 0.3);
  animation: fadeIn 0.5s ease;
}

.balance-amount {
  font-size: 3.5rem;
  font-weight: 700;
  margin: 0.75rem 0;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

/* ==== Action Cards Grid ==== */
.action-cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2.5rem;
}

.action-card {
  text-align: center;
  padding: 2rem;
  transition: all 0.3s ease;
}

.action-card-icon {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  display: inline-block;
  transition: transform 0.3s ease;
}

.action-card:hover .action-card-icon {
  transform: scale(1.1) rotate(5deg);
}

.action-card h3 {
  margin-bottom: 0.5rem;
  font-size: 1.25rem;
}

/* ==== Children List ==== */
.children-list {
  list-style: none;
  display: grid;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.child-item {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 1.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.2s ease;
}

.child-item:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: var(--primary);
}

.child-username {
  font-weight: 600;
  font-size: 1.125rem;
}

.child-balance {
  color: var(--success);
  font-weight: 700;
  font-size: 1.125rem;
}

/* ==== Transactions ==== */
.transaction-table {
  width: 100%;
  border-collapse: collapse;
}

.transaction-table th {
  text-align: left;
  padding: 1rem;
  border-bottom: 2px solid var(--border-color);
  color: var(--text-secondary);
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.8125rem;
  letter-spacing: 0.5px;
}

.transaction-table td {
  padding: 1.25rem 1rem;
  border-bottom: 1px solid var(--border-color);
}

.transaction-table tr {
  transition: background 0.2s ease;
}

.transaction-table tr:hover {
  background: rgba(255, 255, 255, 0.03);
}

.transaction-amount {
  font-weight: 700;
  font-size: 1.125rem;
}

.transaction-amount.positive {
  color: var(--success);
}

.transaction-amount.negative {
  color: var(--danger);
}

/* ==== Loading States ==== */
.spinner {
  width: 48px;
  height: 48px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top-color: var(--primary);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* ==== Mobile Responsive ==== */
@media (max-width: 768px) {
  .dashboard {
    padding: 1.5rem 1rem;
  }

  .dashboard-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .dashboard-title {
    font-size: 1.75rem;
  }

  .balance-amount {
    font-size: 2.5rem;
  }

  .action-cards-grid {
    grid-template-columns: 1fr;
  }

  .modal {
    width: 95%;
    max-height: 95vh;
  }

  .transaction-table {
    font-size: 0.875rem;
  }

  .transaction-table th,
  .transaction-table td {
    padding: 0.75rem 0.5rem;
  }
}

/* ==== Empty States ==== */
.empty-state {
  text-align: center;
  padding: 4rem 2rem;
}

.empty-state-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  opacity: 0.5;
}

.empty-state-title {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
}

.empty-state-description {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
}